import React, { useEffect, useState } from "react";
import { useParams } from 'react-router-dom';
import { useSelector } from 'react-redux';
import { useDispatch } from 'react-redux';
import { getSingleProduct } from "../../redux/action";
import { AddProductInCart } from "../../redux/action";
import { useNavigate } from 'react-router-dom';


const SingleProduct = () => {

    let { id } = useParams();
    const { product } = useSelector(state => state.data)
    let dispatch = useDispatch();
    let navigate = useNavigate();


    const [productcart, setProductCart] = useState({
        initialProduct: 1,
        totalProduct: 1,
        isIncrease: false,
        isDecrase: false,
        productData: []
    });

    useEffect(() => {
        dispatch(getSingleProduct(id))

    }, []);

    const IncreaseProduct = (item) => {

        item = document.getElementById("NumberOfProduct").textContent;
        if (+item < 10) {
            setProductCart(prevItem => ({
                ...prevItem,
                totalProduct: +item + productcart.initialProduct
            }));
        }
        console.log(productcart.totalProduct)
    }

    const DecreaseProduct = (item) => {
        item = document.getElementById("NumberOfProduct").textContent;
        if (+item > 1) {
            setProductCart(prevItem => ({
                ...prevItem,
                totalProduct: +item - productcart.initialProduct
            }));
        }
    }

    const AddProduct = (id) => {
        const productData = {
            ...product,
            totalProduct: productcart.totalProduct
        }
        dispatch(AddProductInCart(productData));
    }


    return (
        <React.Fragment>
            <section className="content_wrapper">
                <div className="content_wrapper_left">
                    <div className="image_section">
                        <div className="small_images" key={product.id}>
                            <img src={product.image} alt="Product_Image"></img><br></br>
                            <img src={product.image} alt="Product_Image"></img><br></br>
                            <img src={product.image} alt="Product_Image"></img><br></br>
                            <img src={product.image} alt="Product_Image"></img><br></br>
                            <img src={product.image} alt="Product_Image"></img>
                        </div>
                        <div className="bigger_image">
                            <img src={product.image} alt="Product_Image"></img>
                        </div>
                    </div>
                    <div className="product_description_title">
                        <h5>{product.title}</h5>
                        <h6>Description</h6>
                        <p>{product.description}</p>
                        <div className="horizontal_line"></div>
                    </div>
                </div>
                <div className="content_wrapper_right">
                    <div className="category_heading">
                        <p>Clothing / Women's / Outwear</p>
                    </div>                    
                    <h5>{product.title}</h5>
                    <div className="price">
                        <p>$ {product.price}</p>
                    </div>
                    <div className="rating">
                    </div>
                    <div className="description">
                        <p>{product.description}</p>
                    </div>
                    <hr></hr>
                    <div className="color">

                    </div>
                    <div className="size">
                        <h6>Size</h6>
                        <div className="size_boxes">
                            <div className="single_size_box">
                                <p>XS</p>
                            </div>
                            <div className="single_size_box">
                                <p>S</p>
                            </div>
                            <div className="single_size_box">
                                <p>M</p>
                            </div>
                            <div className="single_size_box">
                                <p>L</p>
                            </div>
                            <div className="single_size_box">
                                <p>XL</p>
                            </div>
                        </div>
                    </div>

                    <div className="size">
                        <h6>Quantity</h6>
                        <div className="Quantity">
                            <span className={productcart.totalProduct > 1 ? "decrease_quantity" : "decrease_disabled"} onClick={DecreaseProduct}><p>-</p></span>
                            <p id="NumberOfProduct">{productcart.totalProduct}</p>
                            <span className={productcart.totalProduct < 10 ? "increase_quantity" : "increase_disabled"} onClick={IncreaseProduct}><p>+</p></span>
                        </div>
                    </div>
                    <button onClick={() => {AddProduct(product.id); navigate("/Cart")}}>ADD TO CART</button>
                    <div className="save_share">
                        <div className="save">
                            <i class="fa fa-heart-o" aria-hidden="true"></i>
                            <p>Save</p>
                        </div>
                        <div className="Share">
                            <i class="fa fa-share-alt" aria-hidden="true"></i>
                            <p>Share</p>
                        </div>
                    </div>

                    <div className="Details">
                        <h6>Details</h6>
                        <div className="More_Details">
                            <div>
                                <p>Sweat-wicking</p>
                                <p>Lightweight fabric</p>
                            </div>
                            <div>
                                <p>Breathable</p>
                                <p>69% nylon, 31% lycra</p>
                            </div>
                        </div>
                    </div>
                </div>
            </section>
        </React.Fragment>
    )
}

export default SingleProduct;