import React, { useState } from "react";
import edit_icon from "../../assets/edit_icon.png";
import trash_icon from "../../assets/trash_icon.png";
import heart_icon from "../../assets/heart_icon.png";
import { useSelector } from 'react-redux';
import { useDispatch } from 'react-redux';
import { IncreaseItemOfProduct } from "../../redux/action";
import { DecreaseItemOfProduct } from './../../redux/action';


const Cart = () => {

    const { productsInCart } = useSelector(state => state.data)

    const [showEditBox, setShowEditBox] = useState(false);

    let totalprice = 0;

    productsInCart.map((product) => {
        const totalProductprice = product.price * product.totalProduct;
        totalprice = totalprice + totalProductprice;
    });

    let dispatch = useDispatch();

    const IncreaseProduct = (id) => {
        console.log("pID", id);
        const item = document.getElementById("NumberOfProduct").textContent;
        if (+item < 10) {
            dispatch(IncreaseItemOfProduct(id))
        }

    }

    const DecreaseProduct = (id) => {
        const item = document.getElementById("NumberOfProduct").textContent;
        if (+item > 1) {
            dispatch(DecreaseItemOfProduct(id))
        }
    }


    const showEditRemoveBox = () => {
        setShowEditBox(true);
    }

    const hideEditRemoveBox = () => {
        setShowEditBox(false);
    }

    
    return (
        <React.Fragment>
            <section className="wrapper_content">
                <h4>Your Shopping Bag</h4>
                <div className="horizontal_line"></div>
                <div className="product_content">
                    <div className="Product_listing">
                        {
                            productsInCart.map((productData) => {
                                return (

                                    <div className="product_Listing_in_cart">
                                        <div className="Details_Product">
                                            <img src={productData.image} alt="Product_Image"></img>
                                            <div>
                                                <h6>{productData.title}</h6>
                                                <p>{productData.category}</p>
                                                <p>${productData.price} X {productData.totalProduct} </p>
                                            </div>
                                            <div className="three_dots" onClick={() => showEditRemoveBox()}>
                                                <div className="dot"></div>
                                                <div className="dot"></div>
                                                <div className="dot"></div>
                                            </div>
                                        </div>
                                        <div className="Increase_Product">
                                            <div className="Quantity">
                                                <span className="decrease_quantity" onClick={() => DecreaseProduct(productData.id)}><p>-</p></span>
                                                <p id="NumberOfProduct">{productData.totalProduct}</p>
                                                <span className="increase_quantity" onClick={() => IncreaseProduct(productData.id)}><p>+</p></span>
                                            </div>
                                            { !showEditBox && 
                                            <div className="extra_details">
                                            <button onClick={() => hideEditRemoveBox() }>X</button>

                                                <div className="Edit_Remove_Product">
                                                    <img src={edit_icon} alt="Edit_Icon"></img>
                                                    <p>Edit</p>
                                                </div>
                                                <div className="Edit_Remove_Product">
                                                    <img src={trash_icon} alt="trash_icon"></img>
                                                    <p>Remove</p>
                                                </div>
                                                <div className="Edit_Remove_Product">
                                                    <img src={heart_icon} alt="heart_icon"></img>
                                                    <p>Save for Later</p>
                                                </div>
                                            </div>
                                            }

                                        </div>
                                    </div>

                                )
                            })
                        }
                    </div>
                    <div className="Pricing_summary">
                        <div className="Card">
                            <h5>Pricing Summary</h5>

                            <div className="sub_total">
                                <p>Subtotal</p>
                                <p>$ {totalprice.toFixed(2)}</p>
                            </div>
                            <div className="sub_total">
                                <p>Estimated shipping</p>
                                <p>Free</p>
                            </div>
                            <div className="sub_total">
                                <h5>Estimated Total</h5>
                                <h5>$ {totalprice.toFixed(2)}</h5>
                            </div>
                            <button>Checkout</button>
                            <button className="paypalbtn">PayPal</button>
                        </div>
                    </div>
                </div>

            </section>
        </React.Fragment>
    )
}

export default Cart;